<template>
  <el-row :gutter="20">
    <el-col :xs="20" :sm="16" :md="14" :lg="12">
      <el-form :label-position="labelPosition" label-width="80px" >
        <el-form-item label="URL">
          <el-input :value="config.url" v-model="config.url"></el-input>
        </el-form-item>
        <el-form-item label="DB"><el-input type="number" :value="config.db" v-model="config.db"></el-input></el-form-item>
        <el-form-item label="Password"><el-input :value="config.password" v-model="config.password"></el-input></el-form-item>
        <el-form-item label="Timeout"><el-input type="number" :value="config.timeOut" v-model="config.timeOut"></el-input></el-form-item>
        <el-form-item>
          <el-button type="primary" @click="updateConfig">更新</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
// import mockData from '../../mock/mock.js'
export default {
  data () {
    return {
      config: {},
      labelPosition: 'left'
    }
  },
  methods: {
    getConfig () {
      let _this = this
      this.$http.get('/api/config').then(resp => {
        console.log('get config ... ', resp)
        _this.config = resp.body
      })
    },
    updateConfig () {
      console.log('updateConfig ... ', this.config)
      let _this = this
      _this.$http.post('/api/config', _this.config).then(resp => {
        _this.$message({message: 'config changed', type: 'warning'})
        _this.$root.$data.eventHub.$emit('tab-change', 'first')
      })
    },
    onchange (key, newData) {
      // this.config[key] = parseInt(newData, 10)
      this.config[key] = newData
    }
  },
  mounted: function () {
    this.getConfig()
    // this.config = mockData.configMockData
    console.log('config ... ', this.config)
  }
}
</script>



