<template><div>
  <el-row :gutter="23">
    <el-col :span="6">
      <!-- all keys -->
      <div class="grid-content bg-purple">
        <el-tree :data="keysTreeData" :props="treeProps" @node-click="nodeClick"></el-tree>
      </div>
    </el-col>
    <el-col :span="16">
      <!-- detail info -->
      <div class="grid-content bg-purple">
        <el-row>
          <el-form :inline="true" id="home-form" @submit.native.prevent>
            <el-form-item>
              <el-input type="text" placeholder="命令" v-model="redisCmd"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="exec" :disabled="!redisCmd">执行</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="fetchData" >刷新</el-button>
            </el-form-item>
          </el-form>
        </el-row>

        <el-row>
          <el-table :data="tableData" border style="width: 100%" >
              <el-table-column
                prop="name"
                label="key"
              ></el-table-column>
              <el-table-column
                label="value"
              >
                <template scope="scope">
                    <!--<p v-if="scope.row.data && scope.row.data.length == 1" >{{scope.row.data[0].value}}</p>-->
                    <p v-if="valueColumnIf(scope.row)" >{{scope.row.data[0].value}}</p>
                    <p v-else >Object</p>
                </template>
              </el-table-column>
              <el-table-column
                label="type"
              >
                <template scope="scope">
                  <p v-if="scope.row.type">{{scope.row.type}}</p>
                  <p v-else>--</p>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                  <el-button
                    size="small"
                    @click="showDetail(scope.row, scope.$index)"
                  >详细</el-button>
                </template>
              </el-table-column>
          </el-table>
        </el-row>
      </div>
    </el-col>
  </el-row>
  <el-dialog id="detail-dialog" v-model="dialogFormVisible" :title="dialogTitle" @close="dialogClose" @open="dialogOpen">
  <div id="home-dialog-form-label"> <p id="field-label">field</p><p id="value-label">value</p></div>
  <el-form :inline="true" class="demo-form-inline" :model="detailForm">
    <div v-for="(form,idx) in detailForm.data" v-if="detailForm.data&&detailForm.data.length" :key="form">
    <el-form-item >
      <el-input v-if="form.field" :value="form.field" @change="onchange(idx, 4, $event)"></el-input>
      <el-input v-else value="--" v-model="form.field" :disabled="true" ></el-input>
    </el-form-item>
    <el-form-item >
      <el-input :value="form.value" v-on:change="onchange(idx, 3, $event)"></el-input>
    </el-form-item>
    <el-form-item >
      <el-button @click="dialogDel(idx)">删除</el-button>
    </el-form-item>
    </div>
    <el-form-item v-if="detailForm.type !='string'"><el-button @click="dialogDelAll">全部删除</el-button> </el-form-item>
  </el-form>
</el-dialog>
</div></template>

<script>
import { Loading } from 'element-ui'

export default {
  name: 'home',
  data () {
    return {
      keysTreeData: [],
      treeProps: {
        children: 'children',
        label: 'name'
      },
      tableData: [],
      dialogFormVisible: false,
      detailForm: {},
      redisCmd: '',
      oldField: '',
      oldValue: '',
      dialogOpenStatus: false,
      selectedNode: null,
      detailIndex: 0
    }
  },
  computed: {
    dialogTitle: function () {
      let type = this.detailForm.type ? this.detailForm.type : '-'
      return this.detailForm.fullName + '  [' + type + ']'
    }
  },
  methods: {
    exec () {
      // this.$message('exec ... ' + this.redisCmd)
      let _this = this
      _this.$http.post('/api/exec', {cmd: this.redisCmd}).then(resp => {
        console.log('resp after exec ... ', resp)
        let lowerCmd = this.redisCmd.toLowerCase()
        if (lowerCmd.includes('select')) {
          // modify db setting
          _this.$message({message: 'db changed', type: 'warning'})
          let data = JSON.parse(resp.body.resp)
          console.log('data .... ', data)
          _this.keysTreeData = (data && data.children) ? data.children : []
          _this.tableData = (data && data.children) ? [data.children[0]] : []
        } else if (lowerCmd.includes('keys')) {
          // refresh keys tree
          let data = JSON.parse(resp.body.resp)
          console.log('data .... ', data)
          // _this.keysTreeData = (data && data.children) ? data.children : []
          _this.tableData = (data && data.children) ? data.children : []
        } else {
          _this.$message(resp.body.resp)
        }
        // console.log('after exec ..... ', _this.keysTreeData)
      })
    },
    showDetail (row, ctx) {
      this.dialogFormVisible = true
      this.detailForm = row
      this.detailIndex = ctx
      // console.log('ctx .... ', ctx)
    },
    dialogDelAll (data) {
      // console.log('del All , ', this.selectedNode)
      let _this = this
      let req = {fullName: this.detailForm.fullName, type: this.detailForm.type, ope: 2}
      _this.$http.post('/api/modify', req).then(resp => {
        _this.tableData.splice(_this.detailIndex, 1)
        if (_this.tableData.length === 0) {
          // console.log('zero tables', _this.tableData)
          for (let i = 0; i < _this.selectedNode.parent.children.length; i++) {
            if (_this.selectedNode.parent.children[i].fullName === _this.detailForm.fullName) {
              _this.selectedNode.parent.children.splice(i, 1)
              // console.log('found in parent ..... ', i)
            }
          }
        }
        _this.dialogFormVisible = false
      })
    },
    dialogDel (idx) {
      let req = {fullName: this.detailForm.fullName, type: this.detailForm.type, ope: 1}
      if (this.detailForm.data[idx].field) {
        req.field = this.detailForm.data[idx].field
      } else {
        req.field = this.detailForm.data[idx].value
      }
      // console.log('del ... ', idx, ' req .. ', req)
      let _this = this
      _this.$http.post('/api/modify', req).then(resp => {
        _this.detailForm.data.splice(idx, 1)
        if (_this.detailForm.data.length === 0) {
          _this.tableData.splice(_this.detailIndex, 1)
          if (_this.tableData.length === 0) {
            // console.log('zero tables', _this.tableData)
            for (let i = 0; i < _this.selectedNode.parent.children.length; i++) {
              if (_this.selectedNode.parent.children[i].fullName === _this.detailForm.fullName) {
                _this.selectedNode.parent.children.splice(i, 1)
                // console.log('found in parent ..... ', i)
              }
            }
          }
          _this.dialogFormVisible = false
        }
        // console.log('after moidfy ... ', _this.detailForm)
        // console.log('table ....  ', _this.tableData)
      })
    },
    onchange (idx, ope, newData) {
      if (this.dialogOpenStatus) {
        console.log('onchange ....')
        // console.log('onchange ', idx, ' change the env ', ope, ' new value : ', newData)
        let req = {fullName: this.detailForm.fullName, type: this.detailForm.type, ope: ope}
        if (ope === 3) {
          req.oldValue = this.detailForm.data[idx].value
          req.value = newData
          this.detailForm.data[idx].value = newData
        } else {
          req.oldField = this.detailForm.data[idx].field
          req.field = newData
          this.detailForm.data[idx].field = newData
        }
        // console.log('onchange req ... ', this.detailForm.fullName, ' typ', ope, ' new value', newData)
        let _this = this
        _this.$http.post('/api/modify', req).then(resp => {
          _this.$message({message: 'updated', type: 'info'})
        })
      }
    },
    fetchData () {
      // this.$message('fetching data ....')
      let loadingInstance = Loading.service({ body: true })
      let _this = this
      _this.$http.get('/api/api').then(resp => {
        if (resp.body.children && resp.body.children.length) {
          _this.addNodeParent(resp.body, resp.body.children)
          _this.keysTreeData = resp.body.children
        } else {
          _this.keysTreeData = []
        }
        _this.redisCmd = ''
        _this.tableData = []
        loadingInstance.close()
      })
    },
    nodeClick (node) {
      this.selectedNode = node
      // console.log('selectedNode .... ', node)
      if (node.children && node.children.length) {
        let newNode = []
        // filter empty value
        for (let i = 0; i < node.children.length; i++) {
          // console.log('node clicked ... ', node.children[i])
          if (node.children[i].data) {
            newNode.push(node.children[i])
          }
        }
        this.tableData = newNode
      } else {
        this.tableData = [node]
      }
    },
    valueColumnIf (row) {
      // console.log('tableValueIf ... ', row, ' row type ... ', row.type)
      if (row.data) {
        if (row.type === 'string' && row.data && row.data.length) {
          return true
        }
      }
      return false
    },
    dialogClose () {
      console.log('dialogClose ..... ')
      this.detailForm = {}
      this.dialogOpenStatus = false
    },
    dialogOpen () {
      this.dialogOpenStatus = true
    },
    addNodeParent (parent, childs) {
      // console.log('addNodeParent , ', parent, ' childs .. ', childs)
      if (childs && childs.length) {
        for (let i = 0; i < childs.length; i++) {
          childs[i].parent = parent
          this.addNodeParent(childs[i], childs[i].children)
        }
      }
    }
  },
  mounted: function () {
    this.fetchData()
  },
  created: function () {
    let _this = this
    _this.$root.$data.eventHub.$on('home-refresh', function () {
      console.log('home-refresh event .... ')
      _this.fetchData()
    })
  }
}
</script>

<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  #home-form {
    text-align: left;
    height: 15px;
    margin-bottom: 0px;
  }

  #field-label {
    position: absolute;
    left: 101px;
    top: 38px;
  }
  #value-label {
    position: absolute;
    left: 284px;
    top: 38px;
  }
  #home-dialog-form-label p {
    display: inline;
    margin-bottom: 10px;
  }
</style>



